import api from "./api"; // Assurez-vous que ce chemin est correct
import { SearchResponse, DocumentSource } from "../types/search"; // Importer les types ncessaires

export const searchService = {
  // Mthode pour effectuer une recherche avec un terme
  search: async (searchTerm: string): Promise<SearchResponse> => {
    const { data } = await api.get(
      `/search1Highligth/${encodeURIComponent(searchTerm)}`
    );
    return data;
  },

  // Mthode pour obtenir un aperu d'un document
  getDocumentPreview: async (
    documentName: string,
    searchTerm: string
  ): Promise<DocumentSource> => {
    try {
      const { data } = await api.get(
        `/highlightera2/${encodeURIComponent(
          documentName
        )}/${encodeURIComponent(searchTerm)}`
      );
      return data; // Retourner les donnes de prvisualisation
    } catch (error) {
      console.error(
        "Erreur lors de l'obtention de l'aperu du document :",
        error
      );
      throw new Error("chec de l'obtention de l'aperu. Veuillez ressayer."); // Grer les erreurs
    }
  },
};
