import api from "./api";
import type { User } from "../types/auth";

// Interface pour les donnes ncessaires  la cration d'un utilisateur
interface CreateUserData {
  email: string;
  password: string;
  nomUser: string;
  prenomUser: string;
  roles?: string[]; // Optionnel, selon les besoins de l'API
  IsActive?: boolean; // Optionnel, avec une valeur par dfaut ct backend
  Telephone?: string; // Optionnel
}

// Interface pour les donnes ncessaires  la mise  jour d'un utilisateur
interface UpdateUserData {
  email?: string;
  password?: string;
  nomUser?: string;
  prenomUser?: string;
  roles?: string[];
  IsActive?: boolean;
  Telephone?: string;
}

// Service utilisateur
export const userService = {
  // Rcuprer tous les utilisateurs
  getUsers: async (): Promise<User[]> => {
    try {
      const { data } = await api.get("/users");
      if (!Array.isArray(data)) {
        throw new Error("La rponse de l'API n'est pas un tableau.");
      }
      return data.map((user) => ({
        id: user.id || user.idUser, // Grer les deux formats possibles
        email: user.email || user.Email,
        Nom: user.nomUser || user.NomUser,
        Prenom: user.prenomUser || user.PrenomUser,
        roles: user.roles || user.Roles || [],
        IsActive: user.IsActive ?? true, // Valeur par dfaut si undefined
        Telephone: user.Telephone || null,
      }));
    } catch (error) {
      console.error("Erreur lors de la rcupration des utilisateurs :", error);
      throw error;
    }
  },

  // Crer un nouvel utilisateur
  createUser: async (userData: CreateUserData): Promise<User> => {
    try {
      const { data } = await api.post("/users/register", userData);
      return {
        id: data.id || data.idUser,
        email: data.email || data.Email,
        NomUser: data.NomUser || data.NomUser,
        Prenom: data.prenomUser || data.PrenomUser,
        roles: data.roles || data.Roles || [],
        IsActive: data.IsActive ?? true,
        Telephone: data.Telephone || null,
      };
    } catch (error) {
      console.error("Erreur lors de la cration de l'utilisateur :", error);
      throw error;
    }
  },

  // Mettre  jour un utilisateur existant
  updateUser: async (id: string, userData: UpdateUserData): Promise<User> => {
    try {
      const { data } = await api.put(`/users/${id}`, userData);
      return {
        id: data.id || data.idUser,
        email: data.email || data.Email,
        Nom: data.nomUser || data.NomUser,
        Prenom: data.prenomUser || data.PrenomUser,
        roles: data.roles || data.Roles || [],
        IsActive: data.IsActive ?? true,
        Telephone: data.Telephone || null,
      };
    } catch (error) {
      console.error("Erreur lors de la mise  jour de l'utilisateur :", error);
      throw error;
    }
  },

  // Supprimer un utilisateur
  deleteUser: async (id: string): Promise<void> => {
    try {
      await api.delete(`/users/${id}`);
    } catch (error) {
      console.error("Erreur lors de la suppression de l'utilisateur :", error);
      throw error;
    }
  },

  // Rcuprer un utilisateur par son ID
  getUserById: async (id: string): Promise<User> => {
    try {
      const { data } = await api.get(`/users/${id}`);
      if (!data || !data.id) {
        throw new Error("Utilisateur non trouv ou donnes invalides.");
      }
      return {
        id: data.id || data.idUser,
        email: data.email || data.Email,
        Nom: data.nomUser || data.NomUser,
        Prenom: data.prenomUser || data.PrenomUser,
        roles: data.roles || data.Roles || [],
        IsActive: data.IsActive ?? true,
        Telephone: data.Telephone || null,
      };
    } catch (error) {
      console.error(
        "Erreur lors de la rcupration de l'utilisateur par ID :",
        error
      );
      throw error;
    }
  },
};
